    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    
    body {
        background-color: rgb(255, 255, 255);
        height: 100%;
        width: 100%;
    }
    
    .image {
        width: 300px;
        height: 300px;
    }
    
    .loader {
        display: flex;
        margin-left: auto;
        margin-right: auto;
    }
    
    @media screen and (max-width: 768px) {
        .loader>img {
            width: 250px;
            height: 210px;
        }
    }