    border: 1px solid #000000;
    padding: 18px 34px;
    font-size: 18px;
    margin-left: 18px;
    position: relative;
    background-color: white;
    transition: 0.3s ease-in-out;
}

.feedback__btn a {
    color: rgb(0, 0, 0);
}

.feedback__btn:hover {
    background-color: black;
}

.feedback__btn:hover a {
    color: rgb(255, 255, 255);
}

